import React, { Component } from 'react';
import styled from 'styled-components';
import FormInput from './FormInput';

const StyledOverview = styled.div`
    display: grid;
    grid-template-columns: repeat(3, 1fr);
`;

class Overview extends Component {
    render() {
        return (
            <StyledOverview>
                <FormInput name="sessions" niceName="Sessions" onChange={this.handleChange} value={this.state.sessions} placeholder="Add in your sessions" />
                <FormInput name="conversions" niceName="Conversions" onChange={this.handleChange} value={this.state.conversions} placeholder="Add in your conversions" />
                <FormInput name="conversionValue" niceName="Conversion Value" onChange={this.handleChange} value={this.state.conversionValue} placeholder="Add in your conversion value" />
            </StyledOverview>
        );
    }
}

export default Overview;